<template>
  <div class="blog-category-container" v-loading="isLoading">
    <RightList @select="handleSelect" :list="list"/>
  </div>
</template>

<script>
import RightList from "./RightLists.vue"
import fetchDate from "@/fetchDate/fetchDate.js"
import {getBlogTypes} from '@/api/blog'
export default {
  components:{
    RightList,
  },
  mixins:[fetchDate([])],
  methods:{
      async fetchDatas() {
        return await getBlogTypes()
      },
      handleSelect(item)  {
           if(item.id !== -1) {
                            
              this.$router.push({
               name:"CategoryBlog",
               query:{
                 page:1,
                 limit:this.limit,
               },
               params:{
                  categoryId:item.id
               }
             })
            }else{
                this.$router.push({
               name:"Blog",
               query:{
                 page:1,
                 limit:this.limit,
               }
             })
            }
        },
  },
  computed:{
        categoryId() {
        return +this.$route.params.categoryId || -1;
        },
        limit() {
          return +this.$route.query.limit || 10;
        },
        list() {
      const totalArticleCount = this.data.reduce(
        (a, b) => a + b.articleCount,
        0
      );
        let result = [
          {
            name:"全部",
            articleCount:totalArticleCount,
            id:-1,
          },
          ...this.data,
        ];
        return result.map((item)=>({
          ...item,
          isSelect:item.id === this.categoryId,
          aside: `${item.articleCount}篇`,

        }))
        
      }
}


}
</script>

<style scoped lang="less">
.blog-category-container{
     width: 300px;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
  height: 100%;
  overflow-y: auto;
  h2 {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 1em;
    margin: 0;
  }
}
</style>