<template>
  <div class="pager-container" v-if="pageNumber > 1">
      <a :class="{
          disabled:current == 1
      }" v-show="current > 1" @click="handleClick(1)"> |&lt;&lt;</a>
      <a  @click="handleClick(current - 1)"> &lt;&lt;</a>
      <a v-for="(item, i) in nums" :key="i" :class="{active:item == current}" @click="handleClick(item)">{{item}}</a>
      <a @click="handleClick(current + 1)"> &gt;&gt;</a>
      <a  @click="handleClick(pageNumber)">&gt;&gt;|</a>
  </div>
</template>

<script>
export default {
    props:{
        current:{
            type:Number,
            required:false,
            default:1,
        },
        total:{
            type:Number,
            required:false,
            default:0
        },
        limit:{
            type:Number,
            required:false,
            default:10
        },
        visibleNumber:{
            type:Number,
            required:false,
            default:10
        }
    },
    computed:{
        //总页数
        pageNumber(){
            return Math.ceil(this.total / this.limit)
        },
        //显示的最小页数
        visibleMin() {
            let min = Math.floor(this.current - this.visibleNumber / 2)
            if (min < 1) {
                min = 1;
            }
            return min
        },
        visibleMax() {
            let max = this.visibleMin + this.visibleNumber - 1;
            if (max > this.pageNumber) {
                max = this.pageNumber
            }
            return max
        },
        nums () {
            let num = [];
            for (let i = this.visibleMin; i <= this.visibleMax;i++) {
                num.push(i)
            }
            return num;
        }
    },
    methods:{
        handleClick(newpage) {
            if(newpage < 1){
                newpage =1
            }
            if(newpage > this.pageNumber){
                newpage = this.pageNumber
            }
            if(newpage == this.current){
                return
            }
            this.$emit('pageChange',newpage)
        }
    }


}
</script>

<style scoped lang='less'>
@import '~@/styles/var.less';
.pager-container{
    display: flex;
    justify-content: center;
    margin: 20px 0;
     a {
    color: @primary;
    margin: 0 6px;
    cursor: pointer;
    &.disabled {
      color: @lightWords;
      cursor: not-allowed;
    }
    &.active {
      color: @words;
      font-weight: bold;
      cursor: text;
    }
  }
}
</style>