<template>
  <div class="blog-list-container" ref="container" v-loading="isLoading">
    <ul>
      <li v-for="item in data.rows" :key="item.id">
        <div class="thumb" v-if="item.thumb">
          <RouterLink :to="{
            name:'BlogDetail',
            params:{
              id:item.id,
            }
            }">
            <img v-lazy="item.thumb" :alt="item.title" :title="item.title" />
          </RouterLink>
        </div>
        <div class="main">
            <RouterLink :to="{
            name:'BlogDetail',
            params:{
              id:item.id,
            }
            }">
            <h2>{{ item.title }}</h2>
            </RouterLink>
          <div class="aside">
            <span>日期：{{ formatDate(item.createDate) }}</span>
            <span>浏览：{{ item.scanNumber }}</span>
            <span>评论：{{ item.commentNumber }}</span>
            <RouterLink  :to="{
              name:'CategoryBlog',
              params:{
                categoryId:item.category.id
              }}" >{{ item.category.name }}</RouterLink>
          </div>
          <div class="desc">
            {{ item.description }}
          </div>
        </div>
      </li>
     
    </ul>
     <Empty v-if="data.rows.length === 0 && !isLoading" />
    <!-- 分页放到这里 -->
    <Pager
      v-if="data.total"
      :current="routeInfo.page"
      :total="data.total"
      :limit="routeInfo.limit"
      :visibleNumber="10"
      @pageChange="handlePageChange"
    />
  </div>
</template>

<script>
import {getBlogs} from '@/api/blog'
import Pager from '@/components/Pager'
import Empty from '@/components/Empty'
import fetchDate from "@/fetchDate/fetchDate.js"
import formatDate from "@/utils/formatDate.js"
import mainScroll from "@/mainScroll/mainScroll.js"
export default {
    mixins:[fetchDate({totle:0,rows:[]}),mainScroll("container")],
    components:{
        Pager,
        Empty
    },
    methods:{
      formatDate,
        async fetchDatas() {
            return await getBlogs( this.routeInfo.page,
        this.routeInfo.limit,
        this.routeInfo.categoryId)
        },
        handlePageChange(newpage) {
           if(this.routeInfo.categoryId != -1) {
                            
              this.$router.push({
               name:"CategoryBlog",
               query:{
                 page:newpage,
                 limit:this.routeInfo.limit,
               },
               params:{
                  categoryid:this.routeInfo.categoryid
               }
             })
            }else{
                this.$router.push({
               name:"Blog",
               query:{
                 page:newpage,
                 limit:this.routeInfo.limit,
               }
             })
            }
         

        }
    },
    computed:{
      routeInfo() {
        let categoryId = +this.$route.params.categoryId || -1;
        let page = +this.$route.query.page || 1;
       let limit = +this.$route.query.limit || 10;
        return{
            categoryId,
            page,
            limit

        }
      }
    },
    watch:{
      async $route() {
        this.isLoading = true;
              this.$refs.container.scrollTop = 0;
      this.data =  await this.fetchDatas();
       this.isLoading = false;
      }
    }
}
</script>

<style scoped lang="less">
@import "~@/styles/var.less";
.blog-list-container {
  line-height: 1.7;
  position: relative;
  padding: 20px;
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  //平滑滚动
  scroll-behavior: smooth;
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
}
li {
  display: flex;
  padding: 15px 0;
  border-bottom: 1px solid @gray;
  .thumb {
    flex: 0 0 auto;
    margin-right: 15px;
    img {
      display: block;
      max-width: 200px;
      border-radius: 5px;
    }
  }
  .main {
    flex: 1 1 auto;
    h2 {
      margin: 0;
    }
  }
  .aside {
    font-size: 12px;
    color: @gray;
    height: 50px;
    span {
      margin-right: 15px;
    }
  }
  .desc {
    margin: 15px 0;
    font-size: 14px;
  }
}
</style>